/**
 * 讲师信息相关store
 */

import request from "@/utils/request";

const user = {
  namespaced: true,
  state: {
    // 导师信息
    instructorInfo: {},
  },
  mutations: {
    setInstructorInfo(state, instructorInfo) {
      state.instructorInfo = instructorInfo;
    },
  },
  actions: {
    /**
     * 获取讲师信息
     * @param {String} aid 机构ID
     */
    async instructorInfoGetAPI({ commit }, aid) {
      const data = { aid };
      const res = await request("instructor.instructorInfoMy", data);
      commit("setInstructorInfo", res);
    },
  },
};

export default user;
