/**
 * 用户信息相关store
 */

import request from "@/utils/request";
import router from "@/router";

const user = {
  namespaced: true,
  state: {
    // 登录成功返回信息
    loginInfo: {},
  },
  mutations: {
    setLoginInfo(state, loginInfo) {
      state.loginInfo = loginInfo;
    },
  },
  actions: {
    /**
     * 短信验证码登录
     * @param {Object} params 参数
     *  @param {String} phone 手机号
     *  @param {String} code 验证码
     */
    async userAuthPhone({ commit }, params) {
      const data = {
        phone: params.phone,
        code: params.code,
      };
      const res = await request("user.userAuthPhone", data);
      commit("setLoginInfo", res);
      router.replace("/");
    },
  },
};

export default user;
